@import url(https://fonts.googleapis.com/css2?family=Electrolize&display=swap);
.menu{
    width: 100%;
    color: #fff;
    background-color: rgba(41, 41, 46, .25);
    position: fixed;
    top: 0;
    padding: 30px 0px 7px 0px;
}

.menu ul{
    list-style: none;
    float: right;
    margin-right: 15%;
    margin-bottom: 0;
    margin-top: 0;
}

.menu ul li{
    display: inline-block;
}

label[for="bt_menu"]{
    color: #fff;
    cursor: pointer;
    text-align: center;
    display: none;
    font-size: 20pt;
}

.menu a{
    color: #fff;
    padding: 10px;
    text-decoration: none;
    font-size: 20px;
    border-radius: 10px;
}

.menu a:hover{
    color: #000;
    background-color: #fff;
}

.menu a:target{
    color:#1080ff
}

.menuFixo{
    background-color: #29292e;
    padding: 20px 0px 7px 0px;
}

#bt_menu{
    display: none;
}

#logo{
    font-size: 5vh;
    margin-left: 20px;
}

@media(max-width: 768px) {
    .menu ul li{
        width: 100%;
        float: none;
    }
    label[for="bt_menu"]{
        display: block;
        float: right;
    }
    .menu ul{
        display: none;
        transition: all 1s;
        width: 100%;
        text-align: center;
        margin-right: 0;
    }
    .menu #bt_menu:checked ~ ul{
        display: block;
    }
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* -------------TAGS---------------*/
html{
  scroll-behavior: smooth;
  scroll-padding-top: 70px;
}

body{
  font-family: "Electrolize", sans-serif; 
  font-size: 16px;
}

/*--------------CLASSES--------------*/
.container{
  margin-left: 5%;
  margin-right: 5%;
}

.row{
  display:  flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}
.h-center{
  justify-content: center;
}

.text-center{
  text-align: center;
}

.d-none{
  display: none;
}
.col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-10
{
  width: 100%;
}

.icon{
  width: 30px;
  height: 30px;
}

.div-icon{
  max-height: 50px;
}

.div-icon:hover img{
  width: 48px;
  height: 48px;
  border-radius: 5px;
  background-color: #201c1c;
}

.tecnologias{
  font-size: clamp(1em, 1em + .8vw,2em);
}

.foto{
  width: 100%;
  border: 1px solid #000;
  border-radius: 0.8rem;
  box-shadow: 0 0 2vw #111111;
}

.project{
  background-color: #ccf7ff;
  box-shadow: 0 10px 10px #111111;
  margin: 3vw 0 3vw 0;
}
.project > div{
  padding: 3vw 3vw 3vw 3vw;
}
.project > h1{
  padding-top: 3vw;
  letter-spacing: .8vw;
  font-size: 4vw;
}

.project-discription{
  font-size: clamp(1em, 1em + 1vw, 2em);
}

.titulo{
  padding: 1vw;
  margin-top: 1vw;
  background-color: #29292e;
  color: #fff;
  margin-bottom: 5px;
  font-size: clamp(1.5em, 1em + 1vw, 5em);
  text-align: center;
}

.link-gitHub {
  text-decoration: none;
  border-color: #3498db;
  font-size: clamp(1em, 1em + .5vw, 1.5em);
  border-radius: 10px;
  color: #fff;
  box-shadow: 0 0 40px 40px #3498db inset, 0 0 0 0 #3498db;
  transition: all 150ms ease-in-out;
  padding: 10px;
}
.link-gitHub:hover {
  box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
  color: #000;
}
.div-link-gitHub{
  padding-top: 10px;
}

.skills{
  list-style: none;
  font-weight: bold;
  color: #28c0d4;
  text-shadow: -1px -1px #111, -1px 0 #111, -1px 0 #111;
}

/* ----------IDS----------------- */

#contato{
  background-color: #29292e;
  color: #fff;
  padding: 20px 0px 20px 0px;
}

#contato div{
  margin: 10px;
}

#contato a{
  text-decoration: none;
  color: #fff;
  font-size: 16pt;
  padding: 20px;
}

#perfil{
  border-radius: 10px;
  box-shadow: 0 0 10px #111;
  width: 100%;
  border: 1px solid #111111;
}

#div-sbm{
  font-size: clamp(1em, 1em + 1vw, 2em);
  padding: 4%;
}

#sobre > div > div{
  box-shadow: 0px 10px 10px #111111;
  padding: 2%;
}

#home{
  padding-top: 19vw;
  padding-bottom: 19vw;
  background-color: #29292e;
  background-size: cover;
  background-attachment: fixed;
  background-blend-mode: overlay;
  color: #fff;

}

#home h1{
  font-size: clamp(2em, 2em + 3vw, 6em);
}
#home h2{
  font-size: clamp(1em, 1em + 1vw, 3em);
  margin-top: 1vw;
}

/* ------------------------------*/

@media screen and (min-width: 768px){
  .col-md-4{width: 40%;}
  .col-md-5{width: 50%;}
  .col-md-6{width: 60%;}
  .col-md-7{width: 70%;}
  .col-md-10{width: 100%;}
}

